import React, { Component } from 'react'
import { Layout, Button, Tree, Modal, Input } from 'antd'
import MySqlDriver from '../service/mysqlDriver'

export default class DbSetting extends Component {
  constructor () {
    super()
  }

  state = {}

  dbConfig = {
    host: 'localhost',
    port: 3306,
    user: 'root',
    password: 'root'
  }

  closeDbSetting () {}

  changeDbConfig (name, e) {
    this.dbConfig[name] = e.target.value
  }

  connentDb () {
    const mysqldb = new MySqlDriver(this.dbConfig)
    mysqldb.query('select * from role')
  }

  render () {
    return (
      <Modal
        title=''
        visible
        onCancel={this.closeDbSetting.bind(this)}
        onOk={this.connentDb.bind(this)}
      >
        <Input
          name='host'
          placeholder=''
          onChange={this.changeDbConfig.bind(this, 'host')}
          allowClear
        />
        <Input
          name='port'
          placeholder=''
          onChange={this.changeDbConfig.bind(this, 'port')}
          defaultValue={3306}
          allowClear
        />
        <Input
          name='user'
          placeholder='user'
          onChange={this.changeDbConfig.bind(this, 'user')}
          defaultValue='root'
        />
        <Input
          name='password'
          placeholder='password'
          onChange={this.changeDbConfig.bind(this, 'password')}
          allowClear
        />
        <Input
          name='database'
          placeholder='database'
          onChange={this.changeDbConfig.bind(this, 'database')}
          allowClear
        />
      </Modal>
    )
  }
}
