import mysql from 'mysql'

export default class MySqlDriver {
  dbConfig = {}

  constructor (dbConfig) {
    this.dbConfig = dbConfig
  }

  query (sql, data, func) {
    let db = mysql.createConnection(this.dbConfig)
    console.log('config', this.dbConfig)
    db.connect()
    if (func) {
      db.query(sql, data, this.processResult)
    } else {
      db.query(sql, this.processResult)
    }
  }

  processResult (error, result) {
    if (error) {
      console.log('sql error:' + error.message)
      throw error
    }
    console.log('reslut set:' + result)
  }
}
